import { FaShoppingCart, FaSearch } from 'react-icons/fa';
import { Link } from 'react-router-dom';

import './header.scss';

function Header() {
    return (
        <div className="Header">
            <Link to="/">
                <button className="LogoButton">
                    N O U S
                </button>
            </Link>
            <span className="SearchIcon">
                <FaSearch />
            </span>
            <input
                type="text"
                id="header-search"
                placeholder="Search..."
                className="Search"
            />
            <Link
                to="/men"
                className="HeaderPages">
                <span>
                    Contact
                </span>
            </Link>
            <Link
                to="/men"
                className="HeaderPages">
                <span>
                    About
                </span>
            </Link>
            <Link
                to="/men"
                className="HeaderPages">
                <span >
                    Men
                </span>
            </Link>
            <Link
                to="/men"
                className="HeaderPages">
                <span >
                    Women
                </span>
            </Link>
            <Link
                to="/men"
                className="HeaderPages">
                <span >
                    Electronics
                </span>
            </Link>
            <Link
                to="/men"
                className="HeaderPages">
                <span >
                    Jewelery
                </span>
            </Link>
            <span className="CartIcon">
                <FaShoppingCart />
            </span>
        </div >
    );
}

export default Header;
